body{
  background-color: aquamarine;
}
.App{
  background-color: blueviolet;
}
#to-do-form input{
  background-color: rgb(27, 112,137);
  border: 0;
  width: 200px;
  height: 50px;
  padding: 0 20px;
  margin: 20px;
  font-size: 30px;
  border-radius: 10px;
  color: #fff;
}
#to-do-form input::placeholder{
  color: rgba(red, green, blue, alpha);
}
#to-do-form input:focus,#to-do-form button:focus{
  outline: none;
}
#to-do-form button{
  height: 50px;
  width: 80px;
  border-radius: 5px;
  border: 0;
  background-color: rgb(202, 156, 24);
  cursor: pointer;
  font-weight: bolder;
  color: #fff;
}